import React, { Component } from "react";
import Home from "./home/home";
import Footer from "./shared/footer/footer";
import Header from "./shared/header/header";
import SideMenu from './shared/side-menu/side-menu';
import AddNewAdmin from './admin/add-new-admin/add-new-admin';
import GetAllAdmin from './admin/get-all-admin/get-all-admin';
import EditAdmin from './admin/edit-admin/edit-admin';
import { HashRouter as Router, Route, Switch } from "react-router-dom";
class Pages extends Component {
  constructor(props) {
    super(props);
    this.state = {
      routerClass: "col-sm-10",
      sideMenuClass: "col-sm-2"
    };
  }

  receiveFixStyle(value)
  {
    value === true ? this.setState({routerClass:"col-sm-10",sideMenuClass:"col-sm-2"})
    : this.setState({routerClass:"col-sm-11",sideMenuClass:"col-sm-1"})

  }



  render() {
    return (
      <Router>
        <div>
          <Switch>
            <Header />
          </Switch>

          <div className="row" style={{ width: "100%", minHeight: "87vh" }}>
            <div className={this.state.sideMenuClass} style={{background: "#43566e", padding:"0px"}}>
              <Switch>
                <SideMenu fixStyle={(value)=>{
                  this.receiveFixStyle(value)
                }} kelmamab3ota="z3bola" />
              </Switch>
            </div>
            <div className={this.state.routerClass}>
              <Route exact path="/" component={Home} />
              <Route path="/add-new-admin"  component={AddNewAdmin} ></Route>
              <Route path="/all-admins"  component={GetAllAdmin} ></Route>
              <Route path="/edit-admin/:id"  component={EditAdmin} ></Route>

            </div>
          </div>

          <Switch>
            <Footer />
          </Switch>
        </div>
      </Router>
    );
  }
}

export default Pages;
