import React, { Component } from "react";
import {Link} from "react-router-dom";
import './side-menu.sass'
class SideMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      RenderFull: true
    };
  }

  componentWillReceiveProps(props)
  {
    console.log(props.kelmamb3ota);
  }

  render() {
    return (
    <div>{this.RenderSideMenu()}</div>
  );
  }

  RenderSideMenu() {
    if (this.state.RenderFull === true) {
      return (
        <div style={{ height: "100%", minHeight: "87vh",background: "#43566e" }}>
          <div className="sidebar">
            <div className="links">
              <div className="menu-toggle">
                <i
                  className="fa fa-align-justify"
                  onClick={this.OnPresstoggleLess.bind(this)}
                />
              </div>
              <div className="side-Header">
                <h6 style={{textAlign:"center"}}> Wembley Ps</h6>
              </div>
              <div className="side-Header-item">
                <i className="fa fa-home" />
                <Link to="/">Home</Link>
              </div>
              <div
                className="side-Header-item"
                data-toggle="collapse"
                data-target="#admin"
              >
                <i  className="fa fa-user" />
                <h6>Admin</h6>
              </div>
              <div id="admin" className="collapse">
                <div className="page-link ">
                  <i className="fa fa-user-plus" />
                  <Link to="/add-new-admin">Add New Admin</Link>
                </div>
                <div className="page-link">
                  <i className="fa fa-users" />
                  <Link to="/all-admins">All Admin</Link>
                </div>
                <div className="page-link">
                  <i className="fa fa-address-card" />
                  <Link to="/pages/admin/my-profile">My Profile</Link>
                </div>
              </div>
              <div className="side-Header-item">
                <i className="fa fa-television" />
                <Link to="/pages/device">
                 
                  <h6> Device</h6>
                </Link>
              </div>

              <div
                className="side-Header-item"
                data-toggle="collapse"
                data-target="#payment"
              >
                <i className="fa fa-euro" />
                <h6>Payment</h6>
              </div>
              <div id="payment" className="collapse">
                <div className="page-link ">
                  <i className="fa fa-euro" />
                  <Link to="/pages/payment/Open-Time">Open Time</Link>
                </div>
                <div className="page-link ">
                  <i className="fa fa-clock-o" />
                  <Link to="/pages/payment/current-payments">
                    Current Payment
                  </Link>
                </div>
                <div className="page-link">
                  <i className="fa fa-bar-chart" />
                  <Link to="/pages/payment/payment-reports">
                    Payments Reports
                  </Link>
                </div>
              </div>

              <div className="side-Header-item">
                <i className="fa fa-gamepad" />
                <Link to="/pages/game">
                  <h6>Games</h6>
                </Link>
              </div>

              <div className="side-Header-item">
                <i className="fa fa-beer" />
                <Link to="/pages/drink">
                  <h6>Drink</h6>
                </Link>
              </div>

              <div className="side-Header-item">
                <i className="fa fa-user-circle-o" />
                <Link to="/pages/customer">
                  <h6>Customer</h6>
                </Link>
              </div>

              <div className="side-Header-item">
                <i className="fa fa-clock-o" />
                <Link to="/pages/hour-price">
                  <h6>Edit Your Hour Price</h6>
                </Link>
              </div>
              <div className="side-Header-item">
                <i className="fa fa-file-text-o" />
                <Link to="/pages/hour-price">
                  <h6>Print Text</h6>
                </Link>
              </div>
            </div>
          </div>
        </div>
      );
    } else{
      return (
        <div style={{ height: "100%", minHeight: "87vh",background: "#43566e" }}>
        <div className="sidebar" >
    <div className="links">
      <div className="menu-toggle-less">
        <i className="fa fa-align-justify "  onClick={this.OnPresstoggleLess.bind(this)}></i>
      </div>
      <div className="side-Header-item-icon">
      <Link to="/">
      <i className="fa fa-home"></i>
      </Link>
      </div>
      
      <div className="side-Header-item-icon" data-toggle="collapse" data-target="#admin">
        <i className="fa fa-user"></i>
      </div>
      <div id="admin" className="collapse">
        <div className="page-link" >
        <Link to="/add-admin">
      <i className="fa fa-user-plus"></i>
      </Link>
        </div>

        <div className="page-link" >
        <Link to="//all-admins">
      <i className="fa fa-user-plus"></i>
      </Link>
        
        </div>
        <div className="page-link">
        <Link to="/pagesadmin/admin/my-profile">
        <i className="fa fa-address-card"></i>
      </Link>
         
        </div>
      </div>
      <div className="side-Header-item-icon" >

      <Link to="/pagesadmin/device/get-all-devices">
      <i className="fa fa-television"></i>
      </Link>
      </div>
      <div className="side-Header-item-icon" data-toggle="collapse" data-target="#payment">
        <i className="fa fa-euro"></i>
      </div>
      <div id="payment" className="collapse">
        <div className="page-link ">
        <Link to="/pagesadmin/payment/add-payment">
        <i className="fa fa-euro"></i>
      </Link>
        


        </div>
        <div className="page-link">
        <Link to="/pagesadmin/payment/current-payments">
        <i className="fa fa-clock-o"></i>
      </Link>
           
        </div>
        <div className="page-link" >
        <Link to="/pagesadmin/payment/payment-reports">
        <i className="fa fa-bar-chart"></i>
      </Link>
            

        </div>
      </div>
     

      <div className="side-Header-item-icon" >
      <Link to="/pages/game">
      <i className="fa fa-gamepad"></i>
      </Link>
       
      </div>

      <div className="side-Header-item-icon">
      <Link to="/pages/game">
      <i className="fa fa-beer "></i>
      </Link>
      
      </div>

      <div className="side-Header-item-icon">
      <Link to="/pages/customer">
      <i className="fa fa-address-card"></i>
      </Link>
      </div>

      <div className="side-Header-item-icon" >
      <Link to="/pages/hour-price">
      <i className="fa fa-clock-o"></i>
      </Link>
        
      </div>
      <div className="side-Header-item-icon">
      <Link to="/pages/print-text">
      <i className="fa fa-file-text-o"></i>
      </Link>
      
      </div>
    </div>
  </div>
</div>
      )
    }
  }
  OnPresstoggleLess () {
    this.setState({RenderFull:!this.state.RenderFull},()=>{
      this.props.fixStyle(this.state.RenderFull)
    })
  }
}

export default SideMenu;
